.App {
  text-align: center;
  background-color: #ffffff;
  
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #35124a;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

#Top-Dashboard{
  display: flex;
  flex-direction: row;
  height: 750px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#Current-Leaders-Table{
  display: flex;
  flex-direction: column;
  overflow: scroll;
  width: 50%;
  margin: 1%;
}

.Table-Row{
  overflow-x: auto;
}

#Sub-Heading{
  text-align: left;
}